.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  min-width: 220px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  background-color: transparent;
  text-align: center;
  color: white;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 60px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #3dd0d8;
  transition: all 300ms;
}

.btn:hover {
  cursor: pointer;
}

.btn:hover:before {
  opacity: 0;
}

.btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 1;
  z-index: 0;
  transition: all 300ms;
}

.btn--transparent {
  background-color: transparent;
  box-shadow: inset 0 0 0 2px #FFF;
}

.btn--transparent:hover {
  color: #FFF;
  background-color: #3dd0d8;
  box-shadow: inset 0 0 0 2px #3dd0d8;
}

.btn--transparent:before {
  content: none;
}

.btn--bordered-gradient {
  overflow: visible;
  background-color: #1e1d32;
  border-radius: 10px;
}

.btn--bordered-gradient:hover {
  background-color: #3dd0d8;
  color: #fff;
  box-shadow: 0 0 0 2px #3dd0d8;
}

.btn--bordered-gradient:hover:before {
  opacity: 0;
}

.btn--bordered-gradient:before {
  border-radius: 12px;
  width: auto;
  height: auto;
  background-image: linear-gradient(to right, #3dd0d8 0%, #9252EA 100%);
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
}

.btn span {
  z-index: 1;
}

.gradient {
  background-color: #3dd0d8;
  transition: all 300ms;
}

.gradient:hover {
  cursor: pointer;
}

.gradient:hover:before {
  opacity: 0;
}

.gradient:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 1;
  z-index: 0;
  transition: all 300ms;
}

.hamburger {
  display: none;
  position: relative;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

@media only screen and (max-width: 1200px) {
  .hamburger {
    display: flex;
  }
}

.hamburger__icon {
  position: relative;
  margin-top: 12px;
  margin-bottom: 12px;
  user-select: none;
}

.hamburger__icon, .hamburger__icon::before, .hamburger__icon::after {
  display: block;
  width: 40px;
  height: 6px;
  background-color: #3dd0d8;
  outline: 1px solid transparent;
  transition-property: background-color, transform;
  transition-duration: 0.3s;
}

.hamburger__icon::before, .hamburger__icon::after {
  position: absolute;
  content: "";
}

.hamburger__icon::before {
  top: -12px;
}

.hamburger__icon::after {
  top: 12px;
}

.hamburger.active .hamburger__icon {
  background-color: transparent;
}

.hamburger.active .hamburger__icon::before {
  transform: translateY(12px) rotate(45deg);
}

.hamburger.active .hamburger__icon::after {
  transform: translateY(-12px) rotate(-45deg);
}

.hamburger.active .hamburger__icon:before, .hamburger.active .hamburger__icon:after {
  background-color: #3dd0d8;
}

