.slider-arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  width: 10px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  opacity: 0.9;
  transition: all 300ms;
}

.slider-arrow:last-child {
  margin-right: 0;
}

.slider-arrow:after, .slider-arrow:before {
  pointer-events: none;
  content: "";
  position: absolute;
  width: 2px;
  height: 10px;
  background-color: #3dd0d8;
  border-radius: 5px;
  transition: all 300ms;
}

.slider-arrow:after {
  top: 6px;
  transform: rotate(-45deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
}

.slider-arrow:before {
  bottom: 6px;
  transform: rotate(45deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
}

.slider-arrow:hover {
  cursor: pointer;
  opacity: 1;
}

.slider-arrow:hover:after, .slider-arrow:hover:before {
  border-color: #3dd0d8;
}

.slider-arrow--prev {
  left: -55px;
}

@media only screen and (max-width: 1400px) {
  .slider-arrow--prev {
    left: 15px;
  }
}

.slider-arrow--next {
  transform: rotate(180deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
  right: -55px;
}

@media only screen and (max-width: 1400px) {
  .slider-arrow--next {
    right: 15px;
  }
}

.slider-arrow.slick-disabled {
  opacity: 0.5;
  pointer-events: none;
  transition: all 300ms;
}

.slider-dots {
  margin-top: 80px;
  height: 14px;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

.slider-dots ul {
  width: auto !important;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

.slider-dots li {
  box-sizing: border-box;
  width: 14px;
  min-width: 14px;
  height: 14px;
  box-shadow: inset 0 0 0px 1.5px #fff;
  border-radius: 50%;
  background-color: transparent;
  margin-right: 14px;
  transition: all 300ms;
}

.slider-dots li:last-child {
