.upcoming-games {
	display: flex;
	flex-direction: column;

	&__top {
		width: 100%;
		margin-bottom: 85px;
		position: relative;
		@include r(480) {
			margin-bottom: 0; } }

	&__top-slider {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin: 0 -60px;
		@include r(1400) {
			margin: 0;
			width: 100%; } }

	&__top-item {
		display: flex !important;
		justify-content: center;
		padding: 0 60px;
		@include r(768) {
			display: block !important;
			min-width: 180px;
			width: 100%;
			padding: 0; }
		&.slick-current {
			.card-simple {
				@extend .card-simple.active; } } }

	&__bottom {
		width: 100%;
		position: relative; }

	&__bottom-slider {
		display: flex;
		margin: 0 -60px;
		@include r(1300) {
			margin: 0; } }

	&__bottom-item {
		width: 100%;
		padding: 0 60px;
		@include r(1300) {
			padding: 0; } } }
