.card-full {
	width: 100%;
	padding: 90px 100px;
	border-radius: 10px;
	background-color: #292845;
	@include r(1200) {
		padding: 40px 20px; }
	@include r(1000) {
		padding: 0; }

	&__row {
		display: flex;
		align-items: stretch;
		margin: 0 -45px;
		@include r(1200) {
			margin: 0px -15px; }
		@include r(1000) {
			margin: 0;
			position: relative; } }


	&__col {
		padding: 0 45px;
		display: flex;
		flex-direction: column;
		width: 100%;
		max-width: 50%;
		position: relative;
		@include r(1200) {
			padding: 0 15px; }
		@include r(1000) {
			padding: 40px 20px;
			max-width: 100%; }

		&:nth-child(1) {
			max-width: 560px;
			flex-grow: 1;
			@include r(1000) {
				max-width: 100%;
				z-index: 1; } }
		&:nth-child(2) {
			@include r(1000) {
				@include coverdiv;
				z-index: 0; } } }


	&__box {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include r(480) {
			flex-direction: column-reverse;
			align-items: center; }

		.rating {
			@include r(480) {
				margin-bottom: 30px; } } }


	&__tag {
		display: flex; }

	&__tag-item {
		color: $accent;
		font-family: $fr;
		font-size: 14px;
		font-weight: 700;
		line-height: 14px;
		text-transform: uppercase;
		margin-right: 24px;
		text-decoration: none;
		&:last-child {
			margin-right: 0; } }

	&__date {
		color: #c2d4f8;
		font-family: $fr;
		font-size: 14px;
		font-weight: 700;
		line-height: 14px;
		text-transform: uppercase;
		@include r(480) {
			margin-bottom: 30px; } }

	&__title {
		margin-top: 15px;
		text-shadow: 0 2px 4.8px rgba(0, 0, 0, 0.3);
		color: white;
		font-family: $fa;
		font-size: 32px;
		font-weight: 700;
		line-height: 30px;
		text-transform: uppercase;
		@include r(480) {
			text-align: center; } }

	&__content {
		margin: 45px 0 60px;
		p {
			color: #c2d4f8;
			font-family: $fr;
			font-size: 16px;
			font-weight: 300;
			line-height: 26px;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } } }

	&__img-container {
		position: absolute;
		left: 45px;
		top: -20px;
		bottom: -20px;
		right: -100px;
		background-color: #a1abbe;
		border-radius: 10px;
		box-shadow: -5.4px 8.4px 29px rgba(24, 23, 40, 0.2);
		overflow: hidden;
		@include r(1300) {
			right: 0;
			left: 0;
			top: 0;
			bottom: 0; }
		@include r(1000) {
			&:after {
				content: "";
				@include coverdiv;
				background-color: rgba(0, 0, 0, 0.75); } }
		@include r(480) {
			display: none; } }


	&__img {
		@include coverdiv;
		@include coverimg; } }
