.btnTO {
	position: fixed;
	right: 15px;
	bottom: 15px;
	transition: none !important; }

body {
	@include scrollbar {
		width: 12px; }

	@include scrollbar-track;
	@include scrollbar-thumb {
		background-color: $accent;
		border-radius: 2px;
		@include h {
			background-image: $gradient;
			cursor: pointer;
			opacity: 1; } }

	@include scrollbar-track-piece {
		background-color: red;
		background-color: $bgd; } }
