.header {
	z-index: 999;
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
	padding: 25px 15px;
	box-shadow: 3.5px 6.1px 30px rgba(0, 0, 0, 0.57);
	background-color: #1e1d32;
	@include tr;
	&.notVisible {
		transform: translateY(-200%);
		padding: 15px 15px;
		@include tr; }
	&.fixed {
		transform: translateY(0);
		position: fixed;
		padding: 15px 15px;
		@include tr;
		.logo {
			height: 50px; }
		.btn {
			line-height: 50px; } }

	&__container {
		width: 100%;
		max-width: 1750px;
		display: flex;
		align-items: center;
		justify-content: space-between; }


	&__logo {
		display: flex;
		@include r(370) {
			max-height: 50px; } }


	&__block {
		width: 100%;
		display: flex;
		align-items: center;
		@include r(1200) {
			display: none; } }

	&__navigation {
		width: 100%;
		display: flex;
		justify-content: center; }

	&__box {
		display: flex;
		align-items: center; }

	&__action {
		margin-left: 25px; } }


